@media (max-width: 900px) {
  .heading--h1 {
    font-size: 60px;
  }
}

@media (max-width: 750px) {
  .heading--h1, h1 {
    font-size: 50px;
  }
}

@media (max-width: 600px) {
  .heading--h1, h1 {
    font-size: 40px;
  }

  .heading--h2, h2 {
    font-size: 40px;
  }
}

@media (max-width: 450px) {
  .heading--h1, h1 {
    font-size: 30px;
  }

  .heading--h2, h2 {
    font-size: 30px;
  }

	.paragraph, p,
	.link, a, 
	li {
		font-size: 20px;
	}
	
}

@media (max-width: 350px) {

  .heading--h2, h2 {
    font-size: 25px;
  }

  .heading--h3, h3 {
    font-size: 18px;
  }

  .heading--h4, h4 {
    font-size: 18px;
  }
}

@media (max-width: 650px) {
  .reference-slider__quote {
    font-size: 22px;
  }
}

@media (max-width: 550px) {

  .cookie-banner__text,
  .cookie-banner__link {
    font-size: 12px;
  }
}

@media (max-width: 550px) {
  .quote__text {
    font-size: 30px
  }

  .quote__author {
    font-size: 18px;
  }
}

@media (max-width: 600px) {

  .hero__teaser,
  .hero__slogan {
    font-size: 20px;
  }

}

@mixin hidden {
	height: 0;
	width: 0;
	visibility: hidden;
}
* {
  margin: 0;
  box-sizing: border-box;
  scrollbar-width: none;
  appearance: none;
}

*::selection {
  background-color: rgba(3, 116, 182, 0.99);
  color: rgb(255, 255, 255);
  opacity: 0.99;
}

strong::selection {
  text-shadow:
    1px 0 $white,
    -1px 0 $white,
    0 1px $white,
    0 -1px $white;
  color: $blue;
}

*::-webkit-scrollbar {
  display: none;
}

body,
.body {
  background-color: $black;
  position: relative;

  &--bright {
    background-color: $white;
  }

}

.section {
  margin-bottom: 150px;
}

.section__inner {
  max-width: $max-width;
  margin: 0 auto;
  padding-left: 40px;
  padding-right: 40px;
}

.anchor-target {
  position: relative;
  top: -88px;
}

strong {
  color: $blue;
}

img {
  display: block;
  max-width: 100%;
}

button {
  background-color: transparent;
  border: none;
  outline: none;
  padding: 0;
}

@media (max-width: 500px) {

  .section__inner {
    padding-left: 24px;
    padding-right: 24px;
  }

}
.hello-world {
	width: 100%;
	height: 100%;
	min-height: 100vh;
	display: flex;	
	flex-direction: row;
	justify-content: center;
	align-items: center;

	&__text {
		text-decoration: underline;
		text-decoration-color: red;
	}

}